<template>
  <ContentCard :title="title" @go-back="$emit('go-back')">
    <div v-show="!isShowPannelInfoList">
    <div class="btns">
      <el-button type="primary" @click="btnAudit"></el-button>
      <el-button type="primary" @click="btnSave"></el-button>
    </div>

    <div class="split-bar">
      <i class="el-icon-edit"></i>
    </div>

    <el-form ref="form" :rules="rules" :model="form" label-width="90px">
      <el-form-item label="" prop="tabName">
        <el-input v-model="form.tabName" style="width: 240px"/>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="form.tabCategory">
          <el-option label="" value="0"/>
          <el-option label="" value="1"/>
        </el-select>
      </el-form-item>
      <el-form-item label="" v-if="form.tabCategory==='0'">
        <el-radio-group v-model="tabResource"  @change.native.prevent="changeResource">
          <el-radio label="qq"></el-radio>
          <el-radio label="iqiyi"></el-radio>
          <el-radio label="voole"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="pannel">
          <el-option value="" label=""/>
          <el-option v-for="(item, index) in pannelItems" :key="index" :label="item.label" :value="item.value"/>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="product">
          <el-option value="" label=""/>
          <el-option v-for="(item, index) in productItems" :key="index" :label="item.label" :value="item.value"/>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-button type="primary" @click="isShowPannelInfoList=true"></el-button>
      </el-form-item>
      <el-form-item label="">
        <el-input-number v-model="form.priority" :min="1"/><span></span>
      </el-form-item>
    </el-form>
    <div class="table">
      <Table
        :props="table.props"
        :header="table.header"
        :data="table.data"
      />
    </div>
    </div>
    
    <AddBlockFilter v-show="isShowPannelInfoList" @go-back="isShowPannelInfoList=false" @add-block="addBlock"/>
    
  </ContentCard>
</template>

<script>
import AddBlockFilter from './AddBlockFilter'
import { Table } from 'admin-toolkit'
import { Button } from 'element-ui'
export default {
  components: {
    AddBlockFilter,
    Table
  },

  props: {
    editId: Number
  },

  watch: {
    'form.tabCategory': {
      deep: true,
      handler: function(newVal, oldVal){
        if(newVal === '1'){
          this.pannelItems = []
          this.productItems = this.eduProductItems
          this.pannel = ''
          this.product = ''
        }else if(newVal === '0'){
          this.handleTabResourceChange(this.tabResource)
        }
      }
    },
    tabResource:function (value) {
      this.handleTabResourceChange(value)
    }
  },

  data () {
    return {
      title: '',
      rules: {
        tabName: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      form: {
        tabName: '',
        tabResource: 'qq',
        tabCategory: '0',
        priority: 1
      },
      tabResource: 'qq',
      iqiyiSource: [],
      qqSource: [],
      pannel: '',
      pannelItems: [],
      product: '',
      productItems: [],
      eduProductItems: [],
      isShowPannelInfoList: false, //
      blockTable: [],//
      table: {
        props: {},
        data: [],
        header: [
          {
            label: ''
          },
          {
            label: 'ID',
            prop: 'pannelGroupId'
          },
          {
            label: '',
            prop: 'pannelGroupRemark'
          },
          {
            label: '',
            prop: 'duplicateVersion'
          },
          {
            label: '',
            render: (h, {row}) => {
              return h(Button, {
                props: {
                  type: 'warning'
                },
                on: {
                  click: () => {
                    const index = this.table.data.indexOf(row)
                    this.table.data.splice(index, 1)
                  }
                }
              }, '')
            }
          }
        ],
        selected: [],
        selectionType: 'single'
      }
    };
  },

  methods: {
    // 
    btnAudit() {
      this.save(3)
    },
    btnSave() {
      this.save(2)
    },
    save(status) {
      if(this.table.data.length === 0) {
        this.$message('')
        return false
      }
      const form = this.form
      this.$refs.form.validate(valid => {
        if(valid) {
          var jsonStr = {
            tabInfo: {
              tabId: '',
              systemDefault: '',
              tabName: form.tabName,
              tabType: 3,
              tabStatus: status,
              tabResource: form.tabCategory==='0' ? form.tabResource : '',
              currentVersion: ''
            },
            filmDetail: {
              source: form.tabCategory==='0' ? form.tabResource : '',
              product: this.product || '0',
              channel: this.pannel || '0',
              category: form.tabCategory,
              priority: form.priority
            },
            pannelJson: this.table.data
          }
          this.$service.saveFilmDetailPage({jsonStr: JSON.stringify(jsonStr)}, '').then(() => {
            this.$emit('open-list-page')
          })
        }else {
          this.$message('')
          return
        }
      })
    },
    // 
    changeResource(value) {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
      .then(() => {
        this.$message({
          type: 'success',
          message: '!'
        })
        this.form.tabResource = this.tabResource
        this.clearResource()
      })
      .catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
        this.tabResource = this.form.tabResource
      })
    },
    clearResource() {
      this.table.data = []
    },
    // 
    getMediaResourceInfo() {
      return this.$service.getMediaResourceInfo().then(data => {
        var movieData = JSON.parse(decodeURI(data.slice(5,-1)))
        var videoItemModels = movieData.videoItemModels
        // iqiyi
        var iqiyiSource = {
          categoryList: [],
          productList: [],
        }
        iqiyiSource.categoryList = this._arrayingOption(videoItemModels[0].categoryList, 'category_name', 'category_id')
        iqiyiSource.productList = this._arrayingOption(videoItemModels[0].productList, 'source_name', 'source_sign')
        this.iqiyiSource = iqiyiSource

        //tencent
        var qqSource = {
          categoryList: [],
          productList: [],
        }
        qqSource.categoryList = this._arrayingOption(videoItemModels[1].categoryList, 'category_name', 'cc_category_id')
        qqSource.productList = this._arrayingOption(videoItemModels[1].productList, 'source_name', 'source_sign')
        this.qqSource = qqSource
        this.pannelItems = qqSource.categoryList // 
        this.productItems = qqSource.productList
        //->
        this.eduProductItems = this._arrayingOption(videoItemModels[2].productList, 'source_name', 'source_sign')
      })
    },
    // [{label, value}]
    _arrayingOption(arr, label, value) {
      return arr.reduce((result, item) => {
        return result.concat({label: item[label], value: item[value]})
      }, [])
    },
    handleTabResourceChange(value) {
      const { qqSource, iqiyiSource, tabResource } = this
      this.pannel = ''
      this.product = ''
      switch(value){
        case 'qq': {
          this.pannelItems = qqSource.categoryList
          this.productItems = qqSource.productList
          break
        }
        case 'iqiyi': {
          this.pannelItems = iqiyiSource.categoryList
          this.productItems = iqiyiSource.productList
          break
        }
        default: {
          this.pannelItems = []
          this.productItems = []
        }
      }
    },
    addBlock(blockData) {
      const table = this.table
      table.data = []
      const initial = [
        {
          pannelGroupId: '-1002',
          pannelGroupRemark: ''
        },
        {
          pannelGroupId: '-1001',
          pannelGroupRemark: ''
        },
        {
          pannelGroupId: '-1003',
          pannelGroupRemark: ''
        }
      ]
      table.data = table.data.concat(initial)
      table.data = table.data.concat(blockData)
    }
  },
  created() {
    this.getMediaResourceInfo()
    if(this.editId) {
      this.title = ''
    }else {
      this.title = ''
    }
  }

}
</script>

<style lang='stylus' scoped>
.split-bar
  width 96%
  height 36px
  margin 8px 0px
  padding 0px 6px
  font-size 17px
  line-height 36px
  background #e5e9f2
  border-radius 4px
.table
  width 550px
  margin-left 40px
</style>