<template>
  <ContentCard :title="title" @go-back="$emit('go-back')">
    <!---->
    <el-row :gutter="40">
      <el-col :span="24">
        <el-form :model="form" :rules="formRules" ref="form" label-width="100px">
          <el-form-item label="" prop="policyName">
            <el-input v-model="form.policyName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="chipModel">
            <el-button
              v-if="form.deviceInfos.length===0"
              icon="el-icon-plus"
              @click="selectChipModel"
            ></el-button>
            <SelectedTag v-if="form.deviceInfos.length>0">
              <ul>
                <li v-for="(item, index) in form.deviceInfos" :key="index">
                  <el-tag
                    type="success"
                    closable
                    @close="modelChipSelectedRemove(item)"
                  >{{item.model}}_{{item.chip}}</el-tag>
                </li>
                <li>
                  <el-button icon="el-icon-plus" @click="selectChipModel"></el-button>
                </li>
              </ul>
            </SelectedTag>
          </el-form-item>
          <el-form-item label="" required class="homePageVersion">
            <el-col :span="11" style="padding-left:0px;padding-right:0px;">
              <el-form-item prop="homePageVerStart">
                <!-- <el-input
                  placeholder=""
                  v-model="form.homePageVerStart"
                  style="width: 100%;"
                ></el-input>-->

                <el-input-number v-model="form.homePageVerStart" :min="1000000" :max="9999999"></el-input-number>
              </el-form-item>
            </el-col>
            <el-col class="line" :span="1">-</el-col>
            <el-col :span="11" style="padding-left:0px;padding-right:0px;">
              <el-form-item prop="homePageVerEnd">
                <el-input-number v-model="form.homePageVerEnd" :min="1000000" :max="9999999"></el-input-number>
                <!-- <el-input placeholder="" v-model="form.homePageVerEnd" style="width: 100%;"></el-input> -->
              </el-form-item>
            </el-col>
          </el-form-item>
          <el-form-item label="Mac" required>
            <el-col :span="11" style="padding-left:0px;padding-right:0px;">
              <el-form-item prop="macStart">
                <el-input placeholder="Mac" v-model="form.macStart" style="width: 100%;"></el-input>
              </el-form-item>
            </el-col>
            <el-col class="line" :span="1">-</el-col>
            <el-col :span="11" style="padding-left:0px;padding-right:0px;">
              <el-form-item prop="macEnd">
                <el-input placeholder="Mac" v-model="form.macEnd" style="width: 100%;"></el-input>
              </el-form-item>
            </el-col>
          </el-form-item>
          <el-form-item label="" prop="priority">
            <el-input-number v-model="form.priority" placeholder=""></el-input-number>
            <span class="tip"></span>
          </el-form-item>
          <el-form-item label="">
            <el-button icon="el-icon-plus" @click="selectHomePageModel('normal')"></el-button>
            <span v-if="typeof(form.homepageInfoListObj['normal'])!=='undefined'"  class="tip">
               <el-tag
                    type="success"
                  >{{form.homepageInfoListObj['normal'].homepageName}}</el-tag>
              </span>
          </el-form-item>
          <el-form-item label="">
            <el-button icon="el-icon-plus" @click="selectHomePageModel('child')"></el-button>
            <span v-if="typeof(form.homepageInfoListObj['child'])!=='undefined'"  class="tip">
               <el-tag
                    type="success"
                  >{{form.homepageInfoListObj['child'].homepageName}}</el-tag>
              </span>
          </el-form-item>
           <el-form-item label="">
            <el-button icon="el-icon-plus" @click="addHomePage('normal')"></el-button>
           
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitBtn"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
    <el-dialog :title="title" :visible.sync="dialogVisible" width="80%">
      <ChipModel ref="chipModelSelected" v-if="mode==='modelChip'"></ChipModel>
      <HomePageModel :homepageModel="model"  v-if="mode==='HomePageModel'" @row-click="rowClick"></HomePageModel>
      <PolicyManageAddHomePage v-if="mode==='addHomePage'"  :itemType="model" ></PolicyManageAddHomePage>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="dialogSubmit"> </el-button>
      </span>
    </el-dialog>
  </ContentCard>
</template>
<script>
import ChipModel from './../../components/ChipModel'
import SelectedTag from './../../components/SelectedTag'
import HomePageModel from './../../components/HomePageModel'
import PolicyManageAddHomePage from './PolicyManageAddHomePage'
export default {
  components: {
    ChipModel,
    SelectedTag,
    HomePageModel,
    PolicyManageAddHomePage
  },
  props: {
    editId: Number,
    default: null
  },
  data() {
    return {
      title: null,
      mode: null,
      dialogVisible: false,
      addHomePageDialogVisible: false,//addHomePage
      model: 'normal',
      form: {
        policyId: null,
        policyName: null,
        macStart: null,
        macEnd: null,
        homePageVerEnd: null,
        homepageVerStart: null,
        priority: null,
        policyStatus: 3,
        deviceInfos: [], //{chip:'',model:''}
        homepageInfoList: [],
        homepageInfoListObj: {},
        schemeFilterEntity: {},
        regionCityPairs: [],
        specialNormalHp: [],
        specialChildHp: [],
        NormalHomeScheme: '', // 
        ChildHomeScheme: '' // 
      },
      formRules: {
        // 
        policyName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        macStart: [
          { required: true, message: 'MAC', trigger: 'blur' },
          {
            validator: this.$checkMacType,
            message: '12MAC',
            trigger: 'blur'
          }
        ],
        macEnd: [
          { required: true, message: 'MAC', trigger: 'blur' },
          {
            validator: this.$checkMacType,
            message: '12MAC',
            trigger: 'blur'
          }
        ],
        homepageVerStart: [
          { required: true, message: '', trigger: 'blur' }
        ],
        homepageVerEnd: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    /** */
    rowClick(row){
      this.dialogVisible = false
      this.form.homepageInfoListObj[this.model] = {
        homepageId: row.homepageId,
        homepageModel: row.homepageModel,
        homepageName: row.homepageName,
        homepageVersion: row.homepageVersion
      }
    },

    /** 
     * 
    */
    addHomePage(model){
      //this.addHomePageDialogVisible = true
      this.dialogVisible = true
      this.title = ''
      this.mode = 'addHomePage'
      this.model = model
    },
    /*
    
    */
    modelChipSelectedRemove(item) {
      this.form.deviceInfos.splice(item, 1)
    },
    dialogSubmit() {
      this.dialogVisible = false
      switch (this.mode) {
        case 'modelChip':
        debugger
          this.form.deviceInfos = this.$refs.chipModelSelected.selected.map(
            e => {
              return {
                chip: e.chip,
                model: e.model
              }
            }
          )
          break
      }
    },
    selectChipModel() {
      this.dialogVisible = true
      this.title = ''
      this.mode = 'modelChip'
    },
    selectHomePageModel(model) {
      this.dialogVisible = true
      this.title = ''
      this.mode = 'HomePageModel'
      this.model = model
    },
    submitBtn() {
      this.$refs.form.validate(valid => {
        if (valid) {
          this.$service
            .globalCornerIconTypeSave(
              { jsonStr: JSON.stringify(this.form) },
              ''
            )
            .then(data => {
              this.$emit('open-list-page')
            })
        }
      })
    },
    getEditData() {
      let obj = this
      this.$service
        .globalCornerIconTypeDetail({ id: this.editId })
        .then(data => {
          Object.keys(this.form).forEach(v => {
            this.form[v] = data[v]
          })
        })
    }
  },
  created() {
    if (this.editId !== null && this.editId !== undefined) {
      this.title = ''
      this.typeId = this.editId
      this.getEditData()
    } else {
      this.title = ''
    }
  }
}
</script>
<style lang="stylus" scoped>
.homePageVersion >>> .el-input-number
  width: 100%
.tip
  font-size 12px
  margin-left 10px
</style>




