<template>
  <ContentCard class="content">
    <ContentWrapper
      :pagination="pagination"
    >
      <el-form :inline="true" :model="formSearch" class="search">
            <el-form-item>
              <el-select v-model="formSearch.platform" @change="changePlatform" placeholder="">
                <el-option label="" value="yinhe"></el-option>
                <el-option label="" value="tencent"></el-option>
                <el-option label="" value="voole"></el-option>
                <el-option label="" value="youku"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item >
              <el-input v-model="formSearch.model" placeholder="">></el-input>
            </el-form-item>
            <el-form-item  >
              <el-input v-model="formSearch.chip" placeholder="">></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="handleFilterChange"></el-button>
              <el-button type="primary" @click="handleFilterReset"></el-button>
            </el-form-item>
      </el-form>
      <el-collapse>
        <el-collapse-item title="">
          <el-form :model="form" :rules="formRules" ref="form" label-width="100px">
            <el-form-item label="" prop="platform">
              <el-select v-model="form.platform" placeholder="">
                <el-option label="" value="yinhe"></el-option>
                <el-option label="" value="tencent"></el-option>
                <el-option label="" value="voole"></el-option>
                <el-option label="" value="youku"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="model">
              <el-input v-model="form.model" placeholder="">></el-input>
            </el-form-item>
            <el-form-item label="" prop="chip">
              <el-input v-model="form.chip" placeholder="">></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="addModelChip"></el-button>
            </el-form-item>
          </el-form>
        </el-collapse-item>
        <el-collapse-item title="">
          <SelectedTag>
            <ul>
              <li v-for="item in selected" :key="item.devParId">
                <el-tag
                  type="success"
                  closable
                  @close="handleRowSelectionRemove(item)"
                >{{item.model}}_{{item.chip}}</el-tag>
              </li>
            </ul>
          </SelectedTag>
        </el-collapse-item>
      </el-collapse>
      <Table
        :props="table.props"
        :header="table.header"
        :data="table.data"
        :selected="table.selected"
        :selection-type="table.selectionType"
        @row-selection-add="handleRowSelectionAdd"
        @row-selection-remove="handleRowSelectionRemove"
        @all-row-selection-change="handleAllRowSelectionChange"
      />
    </ContentWrapper>
  </ContentCard>
</template>
<script>
import _ from 'gateschema'
import { Button } from 'element-ui'
import {
  ContentWrapper,
  Table,
  ActionList,
  utils,
  RemoteSelect
} from 'admin-toolkit'
import SelectedTag from './SelectedTag'
export default {
  components: {
    ActionList,
    Table,
    ContentWrapper,
    SelectedTag
  },
  data() {
    let _this = this
    return {
      platform: '',
      form: {
        platform: null,
        model: null,
        chip: null
      },
      formRules: {
        platform: [{ required: true, message: '', trigger: 'blur' }],
        model: [{ required: true, message: '', trigger: 'blur' }],
        chip: [{ required: true, message: '', trigger: 'blur' }]
      },
      formSearch: {
        sort: undefined,
        order: undefined,
        platform: 'tencent',
        chip: undefined,
        model: undefined
      },
      pagination: {
        pageSize: 5
      },
      selected: [],
      table: {
        props: {},
        header: [
          {
            label: 'ID',
            prop: 'devParId'
          },
          {
            label: '',
            prop: 'platform',
            render: (h, { row }) => {
              return this.$platform(row.platform)
            }
          },
          {
            label: '',
            prop: 'chip'
          },
          {
            label: '',
            prop: 'model'
          }
        ],
        data: [],
        selected: [],
        selectionType: 'multiple'
      }
    }
  },
  // watch: {
  //   'filter.platform': function(newV,oldV){
  //     debugger
  //      if(window.confirm("")){
  //         this.platform = newV
  //         this.fetchData()
  //      } else {
  //        this.filter.platform = oldV
  //      }
  //   }
  // },
  methods: {
    changePlatform($event){ 
      debugger
      if(window.confirm("")){
         // this.platform = newV
          this.fetchData()
       } else {
        // this.formSearch.platform = oldV
       }
    },
    addModelChip() {
      this.$refs.form.validate(valid => {
        if (valid) {
          this.$service.addChipAndModel(this.form, '').then(data => {
            this.fetchData()
          })
        }
      })
    },
    //
    handleRowSelectionAdd(targetItem) {
      this.selected.push(targetItem)
      this.updateTableSelected()
    },
    handleRowSelectionRemove(targetItem) {
      this.selected = this.selected.filter(item => {
        return item.devParId !== targetItem.devParId
      })
      this.updateTableSelected()
    },
    handleAllRowSelectionChange(value) {
      if (value) {
        this.table.data.forEach(this.handleRowSelectionAdd)
      } else {
        this.unselectedIds = this.table.data.map(e => {
          return e.devParId
        })
        this.selected = this.selected.filter(e => {
          if (this.unselectedIds.indexOf(e.devParId) < 0) {
            return e
          }
        })
        this.table.selected = []
      }
    },
    handleAllRowSelectionRemove() {
      this.selected = []
      this.table.selected = []
    },
    updateTableSelected() {
      const table = this.table
      const newSelectedIndex = this.selected.map(e => {
        return e.devParId
      })
      table.selected = table.data.reduce((result, item, index) => {
        if (newSelectedIndex.indexOf(item.devParId) > -1) {
          result.push(index)
        }
        return result
      }, [])
    },
    //
    handleFilterChange() {
        if (this.pagination) {
          this.pagination.currentPage = 1
        }
      this.fetchData()
    },
    //
    handleFilterReset() {
      this.formSearch = {
        sort: undefined,
        order: undefined,
        platform: 'tencent',
        chip: undefined,
        model: undefined
      }
      this.fetchData()
    },
    parseFilter() {
      const { formSearch, pagination } = this
      if (pagination) {
        formSearch.page = pagination.currentPage
        formSearch.rows = pagination.pageSize
      }
      return formSearch
    },
    /**
     * 
     */
    fetchData() {
      this.table.selected = []
      const filter = this.parseFilter()
      this.$service.getChipAndModelPageList(filter).then(data => {
        this.pagination.total = data.total
        this.table.data = data.rows
      })
    }
  },
  created() {
    let filterSchema = _.map({
      platform: _.o
        .enum({
          : 'tencent',
          : 'yinhe',
          : 'voole',
          : 'youku'
        })
        .other('form', {
          component: 'Select',
          placeholder: '',
          cols: {
            item: 3,
            label: 0
          }
        }),
      model: _.o.string.other('form', {
        component: 'Input',
        placeholder: '',
        cols: {
          item: 3,
          label: 0
        }
      }),
      chip: _.o.string.other('form', {
        component: 'Input',
        placeholder: '',
        cols: {
          item: 3,
          label: 0
        }
      })
    }).other('form', {
      layout: 'inline',
      footer: {
        cols: {
          label: 0,
          wrapper: 24
        },
        showSubmit: true,
        submitText: '',
        showReset: true,
        resetText: ''
      }
    })
    this.filterSchema = filterSchema
    this.fetchData()
  }
}
</script>

