<template>
  <ContentCard :title="title" @go-back="$emit('go-back')">
    <!---->
    <el-row :gutter="40">
      <el-col :span="24">
        <el-form :model="form" :rules="formRules" ref="form" label-width="160px">
          <!-- <el-form-item label="ID" prop="commonOnclickId">
            <el-input v-model="form.commonOnclickId" placeholder="ID"></el-input>
          </el-form-item> -->
          <el-form-item label="" prop="onlickName">
            <el-input v-model="form.onlickName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="remark">
            <el-input v-model="form.remark" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="json" prop="onlickJson">
            <el-input type="textarea" v-model="form.onlickJson" placeholder="(exception)"></el-input>
          </el-form-item>
          <el-form-item label="" prop="packagename">
            <el-input v-model="form.packagename" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="versioncode">
            <el-input v-model="form.versioncode" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="dowhat">
            <el-select v-model="form.dowhat" placeholder="">
              <el-option label="startActivity" value="startActivity"></el-option>
              <el-option label="startService" value="startService"></el-option>
              <el-option label="sendBroadcast" value="sendBroadcast"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="bywhat">
            <el-select v-model="form.bywhat" placeholder="">
              <el-option label="action" value="action"></el-option>
              <el-option label="class" value="class"></el-option>
              <el-option label="uri" value="uri"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="byvalue">
            <el-input v-model="form.byvalue" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="params">
            <ul class="cc-params-ul">
              <li v-for="(item, index) in paramsList">
                <el-input v-model="item.key" :value="item.key" placeholder="key"></el-input>
                <el-input v-model="item.value" :value="item.value" placeholder="value"></el-input>
                <el-button v-show="paramsList.length > 1" type="danger" size="mini" @click="paramsRedBtn(index)" title="">-</el-button>
              </li>
            </ul>
            <div class="cc-params-btn">
              <el-button type="primary" size="mini" @click="paramsAddBtn" title="">+</el-button>
            </div>
          </el-form-item>
          <el-form-item label="(exception)" prop="exception">
            <el-input type="textarea" v-model="form.exception" placeholder="(exception)"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitBtn"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
  </ContentCard>
</template>
<script>
export default {
  props: {
    editId: Number,
    default: null
  },
  //  prop: ['editId'],
  data() {
    return {
      title: null,
      form: {
        //commonOnclickId: null,
        onlickName: null,
        remark: null,
        onlickJson: null,
        packagename: null,
        versioncode: '-1',
        dowhat:'startActivity',
        bywhat:'action',
        byvalue:null,
        params:null,
        exception:null
      },
      formRules: {
        //
        onlickName: [{ required: true, message: "", trigger: "blur" }],
        packagename: [{ required: true, message: "", trigger: "blur" }],
        byvalue: [{ required: true, message: "", trigger: "blur" }]
      },
      paramsList: [{
        key: '',
        value: ''
      }]
    };
  },
  methods: {
    paramsAddBtn() {
      this.paramsList.push({
        key: '',
        value: ''
      })
    },
    paramsRedBtn(index) {
      this.paramsList.splice(index, 1)
    },
    submitBtn() {
      this.$refs.form.validate(valid => {
        if (valid) {
          let params = {}
          let jsonStr = {}
          this.paramsList.forEach(function (item) {
            if(item.key.trim() != '') {
              params[item.key] = item.value;
            }
          })
          this.form.params = JSON.stringify(params)
          jsonStr.jsonStr = JSON.stringify(this.form)
          console.log(this.form)
          this.$service.commonOnclickInfoSave(jsonStr, "").then(data => {
            this.$emit("openListPage");
          });
        }
      });
    },
    getEditData() {
      let obj = this;
      this.$service.getCommonOnclickInfoEdit({ id: this.editId }).then(data => {
        Object.keys(this.form).forEach(v => {
          obj.form[v] = data[v];
          if (v === 'params') {
            let arrs = []
            let lists = JSON.parse(data[v])
            console.log(lists)
            Object.keys(lists).forEach(p => {
              let jsons = {}
              jsons.key = p
              jsons.value = lists[p]
              arrs.push(jsons)
            })
            if (data[v] !== '{}') {
              obj.paramsList = arrs
            }
          }
        });
      });
    },
  },
  created() {
    if (this.editId !== null && this.editId !== undefined) {
      this.title = "";
      this.getEditData();
    } else {
      this.title = "";
    }
  }
};
</script>
<style lang="stylus" scoped>
.cc-params-ul, .cc-params-ul li 
  list-style none
  margin 0
  padding 0
.cc-params-ul li 
  margin-bottom 8px
.cc-params-ul li > div 
  display inline-block
  width 20%
  margin-right 5px
</style>
